:host {
  display: block;
  font-family: var(--font-inter, 'Inter', -apple-system, BlinkMacSystemFont, sans-serif);
}

/* Input Wrapper */
.input-wrapper {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm, 8px);
  width: 100%;
}

/* Input Container */
.input-container {
  display: flex;
  align-items: center;
  gap: var(--spacing-md, 12px);
  padding: var(--spacing-md, 12px);
  height: 36px;
  width: 100%;
  background: var(--color-white, #FFFFFF);
  border: 1px solid var(--color-neutral-300, #D0D5DD);
  border-radius: var(--spacing-md, 8px);
  transition: all 0.2s ease-in-out;
  cursor: text;
  box-sizing: border-box;
}

.input-container.input-container-with-leading {
  padding: var(--spacing-md, 12px) var(--spacing-lg, 16px);
}

/* Input States */
.input-container.input-container-hover {
  border-color: var(--color-brand-500, #7F56D9);
}

.input-container.input-container-focused {
  border-color: var(--color-brand-500, #7F56D9);
  box-shadow: 0 0 0 1px var(--color-brand-500, #7F56D9);
}

.input-container.input-container-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background: var(--color-neutral-50, #F9FAFB);
}

.input-container.input-container-error {
  border-color: var(--color-error-300, #FDA29B);
}

.input-container.input-container-error.input-container-hover {
  border-color: var(--color-error-500, #F05250);
}

.input-container.input-container-error.input-container-focused {
  border-color: var(--color-brand-600, #6941C6);
}

/* Input Content */
.input-content {
  display: flex;
  align-items: center;
  gap: var(--spacing-md, 12px);
  flex: 1;
  height: 100%;
  min-width: 0;
}

/* Input Element */
.input-element {
  flex: 1;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  font-family: inherit;
  font-weight: 400;
  font-size: var(--text-sm-size, 14px);
  line-height: 1.43;
  color: var(--color-neutral-800, #1D2939);
  min-width: 60px;
  padding: 0;
}

.input-element:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

.input-element::placeholder {
  color: var(--color-neutral-500, #667085);
}

.input-element:disabled {
  cursor: not-allowed;
}

/* Leading Icon */
.leading-icon {
  font-size: var(--text-xl-size, 20px);
  color: var(--color-neutral-500, #667085);
  user-select: none;
  flex-shrink: 0;
  cursor: default;
  transition: color 0.2s ease-in-out;
}

.input-container-focused .leading-icon {
  color: var(--color-brand-600, #6941C6);
}

/* Visibility Toggle */
.visibility-toggle {
  font-size: var(--text-xl-size, 20px);
  color: var(--color-neutral-400, #98A2B3);
  cursor: pointer;
  user-select: none;
  flex-shrink: 0;
  transition: color 0.2s ease-in-out;
}

.visibility-toggle:hover {
  color: var(--color-neutral-600, #475467);
}

.input-container-disabled .visibility-toggle {
  cursor: not-allowed;
  pointer-events: none;
}

/* Hint Text */
.hint-text {
  font-family: inherit;
  font-weight: 400;
  font-size: var(--text-sm-size, 14px);
  line-height: 1.43;
  color: var(--color-neutral-600, #475467);
}

.hint-text.hint-error {
  color: var(--color-error-600, #DC2626);
}

/* Material Symbols */
.material-symbols-outlined {
  font-family: 'Material Symbols Outlined';
  font-weight: normal;
  font-style: normal;
  font-size: 20px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 24;
}
